<template>
  <div class="box">
    <van-search
      v-model="value"
      shape="round"
      background="white"
      placeholder="搜索你想要的"
      right-icon="search"
      input-align="center"
      @focus="goSearch"
    />
    <!-- 轮播图 -->
    <div class="banner">
      <van-swipe @change="onChange" autoplay="2222">
        <van-swipe-item v-for="(item,index) in bannerImages" :key="index">
          <img :src="item.bannerImg" alt />
        </van-swipe-item>
        <template #indicator>
          <div class="custom-indicator">{{ current + 1 }}/4</div>
        </template>
      </van-swipe>
    </div>
    <!-- 分类类容 -->
    <div class="content">
      <!-- 左边导航 -->
      <div class="left">
        <van-sidebar v-model="$store.state.activeKey">
          <div v-for="(item,index) in typeDes" :key="index">
            <van-sidebar-item :title="item.typeDesc" @click="toggleAside(item,index)" />
          </div>
        </van-sidebar>
      </div>
      <!-- 右边详情 -->
      <div class="right">
        <div class="main" v-for="(item,index) in product" :key="index" @click="goDetalis(item.pid)">
          <div class="item">
            <div class="img fl">
              <img v-lazy="item.smallImg" alt />
            </div>
            <div class="fl text">
              <p>{{item.name}}</p>
              <p>{{item.enname}}</p>
              <span>￥ {{item.price}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("classifyModule");
export default {
  data() {
    return {
      current: 0,
      //轮播图数据
      bannerImages: [],
      // 侧标类型秒速
      typeDes: [],
      //主体内容
      product: [],
      value: ""
    };
  },
  computed: {
    ...mapState(["bannerData", "typedesData", "productData"])
  },
  methods: {
    ...mapMutations(["changeData", "changeProductData"]),
    onChange(index) {
      this.current = index;
    },
    goSearch() {
      this.$router.push({ name: "搜索" });
    },
    //获取侧边标签
    getAsidetype() {
      if (this.typedesData.length > 0) {
        //如果vuex中有数据则直接赋值
        this.typeDes = this.typedesData;
        
        
        return;
      }
      this.axios({
        method: "get",
        url: "/type",
        params: {
          appkey: this.appkey
        }
      })
        .then(res => {
          if (res.data.code == 400) {
            res.data.result.unshift({
              type: "isHot",
              typeDesc: "最新推荐",
              isActive: true
            });
            this.typeDes = res.data.result;
            this.changeData({
              key: "typedesData",
              value: res.data.result
            });
            //  
          }
        })
        .catch(err => {
          
        });
    },
    //获取主要banner
    getBanner() {
      //
      //判断bannerData是否被缓存
      if (this.bannerData.length > 0) {
        this.bannerImages = this.bannerData;
        //
        return;
      }
      this.axios({
        method: "get",
        url: "/banner",
        params: {
          appkey: "U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA="
        }
      })
        .then(res => {
          //
          if (res.data.code == 300) {
            this.bannerImages = res.data.result;

            this.changeData({
              key: "bannerData",
              value: res.data.result
            });
            // 
          }
        })
        .catch(err => {
          
        });
    },
    toggleAside(item, index) {
      
      this.$store.state.activeKey = index;
      this.gettypeDec(item);
    },
    //获取商品描述
    gettypeDec(item) {
      // 
      // 在缓存获取商品数据
      if (this.productData[item.type]) {
        this.product = this.productData[item.type];
        //  
        // 
        return;
      }
      this.product = [];
      let params = {
        appkey: this.appkey,
        key: "type",
        value: item.type
      };
      if (item.type == "isHot") {
        params.key = "isHot";
        params.value = 1;
      }
      this.axios({
        method: "get",
        url: "/typeProducts",
        params
      })
        .then(res => {
          if (res.data.code == 500) {
            
            this.product = res.data.result;
            this.changeProductData({
              type: item.type,
              value: res.data.result
            });
            // 
          }
        })
        .catch(err => {
          
        });
    },

    goDetalis(pid) {
      //
      this.$router.push({
        path: "/detalis",
        query: {
          pid
        }
      });
    }
  },

  created() {
    this.$store.state.active = 1;
    this.getBanner();
    this.getAsidetype();
    this.gettypeDec({ type: "isHot" });
  }
};
</script>

<style lang="less" scoped>
.box {
  position: fixed;
  top: 0;
  bottom: 50px;
  left: 0;
  width: 100%;
  .van-search {
    background-color: #f8f8f8;
    border: 1px solid #f7f7f7;
    /deep/ .van-field__left-icon {
      margin-right: 0.13333rem;
      display: none;
    }
  }
}
.banner {
  width: 98%;
  margin: 0 auto;
  img {
    width: 98%;

    border-radius: 10px;
    display: block;
    margin: 0 auto;
  }
}
.custom-indicator {
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 2px 5px;
  font-size: 12px;
  background: rgba(0, 0, 0, 0.1);
}

.content {
  height: calc(~"100% - 215px");
  width: 100%;
}
.left {
  float: left;
  width: 85px;
}
.right {
  width: calc(~"100% - 85px");
  float: right;
  //   margin: 0;
  //   padding: 0;
  overflow-y: auto;
  height: 100%;
  margin-top: 10px;
  margin-bottom: 60px;
  .item {
    float: left;
    width: 100%;
    margin-bottom: 8px;
    .img {
      width: 35%;
      img {
        width: 100%;
        border-radius: 15px;
      }
    }
    p {
      padding: 0;
      margin-left: 25px;
      margin-top: 8px;
      color: 15px;
    }
    span {
      color: red;
      display: block;
      margin-top: 15px;
      font-size: 15px;
    }
    .text {
      float: left;
      width: 50%;
      line-height: 20px;
      text-align: center;
    }
  }
}
</style>